// components/Modal/Modal.tsx
'use client';

import { useState, useEffect, type ReactNode } from 'react';
import { createPortal } from 'react-dom';
import css from './Modal.module.css';

interface ModalProps {
  children: ReactNode;
  onClose: () => void;
}

const Modal = ({ children, onClose }: ModalProps) => {
  // Стан для зберігання DOM-вузла. Початково - null.
  const [portalNode, setPortalNode] = useState<HTMLElement | null>(null);

  // useEffect виконається тільки на клієнті, коли компонент буде змонтовано
  useEffect(() => {
    // Знаходимо DOM-вузол і зберігаємо його в стані
    const node = document.getElementById('root-modal');
    if (node) {
      setPortalNode(node);
    }

    // Додаємо стилі та обробник Escape
    document.body.style.overflow = 'hidden';
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.code === 'Escape') {
        onClose();
      }
    };
    window.addEventListener('keydown', handleKeyDown);

    // Функція очищення, яка виконається при розмонтуванні компонента
    return () => {
      document.body.style.overflow = 'auto';
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [onClose]); // Залежність від onClose

  const handleBackdropClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (e.currentTarget === e.target) {
      onClose();
    }
  };

  // Якщо DOM-вузол ще не знайдено (наприклад, під час SSR), нічого не рендеримо
  if (!portalNode) {
    return null;
  }

  // Якщо вузол знайдено, створюємо портал
  return createPortal(
    <div
      className={css.backdrop}
      role="dialog"
      aria-modal="true"
      onClick={handleBackdropClick}
    >
      <div className={css.modal}>{children}</div>
    </div>,
    portalNode
  );
};

export default Modal;